.game-image {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	background-size: cover;
	background-position: center;
	overflow: hidden;
	position: relative;

}

.game-gradient-overlay {
	background: -webkit-gradient(linear, left top, left bottom, color-stop(75%, transparent), to(#111));
	background: -webkit-linear-gradient(transparent 75%, #111);
	background: -o-linear-gradient(transparent 75%, #111);
	background: linear-gradient(transparent 75%, #111);
}

.game-info {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	width: 100%;
	padding: 0 20px;
	position: absolute;
	top: 0;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
	color: #bbb;
	pointer-events: none;
	text-align: center;
}

.game-state {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: end;
	    align-items: flex-end;

	position: absolute;
	top: 0;
	width: 100%;
	margin: 0 auto;
}

.game-state > div {
	padding: 20px;
}

.game-state h1 {
	color: #fff;
	text-shadow: 2px 2px 2px rgba(0,0,0,.6);
	margin-bottom: 0.4em;
}

.game-state p {
	background-color: rgba(255,255,255,.8);
	-webkit-box-shadow: 2px 2px 2px rgba(0,0,0,.5);
	        box-shadow: 2px 2px 2px rgba(0,0,0,.5);
	padding: 2px 8px;
	border-radius: 3px;
	color: #000;
}

.game-state button {
	margin-bottom: 0;
	-webkit-box-shadow: 2px 2px 2px rgba(0,0,0,.5);
	        box-shadow: 2px 2px 2px rgba(0,0,0,.5);
}


