
.component-loader {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
	height: 100%;
}

.component-loader span {
  width: 14px;
  height: 14px;
  border-radius: 50%;
  margin: 3px;
  background: #555;
}

.component-loader span:nth-child(1) {
  -webkit-animation: 1200ms ease-in-out infinite 0ms jump;
          animation: 1200ms ease-in-out infinite 0ms jump;
}

.component-loader  span:nth-child(2) {
  -webkit-animation: 1200ms ease-in-out infinite 200ms jump;
          animation: 1200ms ease-in-out infinite 200ms jump;
}

.component-loader  span:nth-child(3) {
  -webkit-animation: 1200ms ease-in-out infinite 400ms jump;
          animation: 1200ms ease-in-out infinite 400ms jump;
}

@-webkit-keyframes jump {
  0% {
		-webkit-transform: translateY(0);
		        transform: translateY(0);
  }
  20% {
		-webkit-transform: translateY(-14px);
		        transform: translateY(-14px);
  }
  60% {
		-webkit-transform: translateY(0);
		        transform: translateY(0);
  }
}

@keyframes jump {
  0% {
		-webkit-transform: translateY(0);
		        transform: translateY(0);
  }
  20% {
		-webkit-transform: translateY(-14px);
		        transform: translateY(-14px);
  }
  60% {
		-webkit-transform: translateY(0);
		        transform: translateY(0);
  }
}



