.component-answers {
	width: 100%;

	position: absolute;
	bottom: 0;
	left: 0;
}

.component-answers > div {
	max-width: 640px;
	margin: 0 auto;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	-ms-flex-pack: space-evenly;
	    justify-content: space-evenly;
	width: 100%;
}

.component-answer {
	-ms-flex-preferred-size: calc(50% - 15px);
	    flex-basis: calc(50% - 15px);
}

.component-answer > div:hover {
	background: rgba(235, 235, 235, 0.5);
	border-color: #bbb;
}


